import React, { useEffect, useState } from "react";
import "./CovidData.css";
import './index.css'

function CovidData() {
const [country, setCountry] = useState("");
const [cases, setCases] = useState("");
const [recovered, setRecovered] = useState("");
const [deaths, setDeaths] = useState("");
const [todayCases, setTodayCases] = useState("");
const [deathCases, setDeathCases] = useState("");
const [recoveredCases, setRecoveredCases] = useState("");
const [userInput, setUserInput] = useState("");

useEffect(() => {
	fetch("https://corona.lmao.ninja/v2/countries")
	.then((res) => res.json())
	.then((data) => {
		setData(data);
	});
}, []);

const setData = ({
	country,
	cases,
	deaths,
	recovered,
	todayCases,
	todayDeaths,
	todayRecovered,
}) => {
	setCountry(country);
	setCases(cases);
	setRecovered(recovered);
	setDeaths(deaths);
	setTodayCases(todayCases);
	setDeathCases(todayDeaths);
	setRecoveredCases(todayRecovered);
};

const handleSearch = (e) => {
	setUserInput(e.target.value);
};
const handleSubmit = (props) => {
	props.preventDefault();
	fetch(`https://corona.lmao.ninja/v2/countries/${userInput}`)
	.then((res) => res.json())
	.then((data) => {
		setData(data);
	});
};

return (
    <center>
	<div className="covidData">
	<h1 class="text-3xl font-bold ">COVID-19 CASES COUNTRY WISE</h1>
	<div className="covidData__input">
		<form onSubmit={handleSubmit}>
		{/* input county name */}
		<input onChange={handleSearch} placeholder="Enter Country Name" />
		<br />
		<button type="submit">Search</button>
		</form>
	</div>

	{/* Showing the details of the country */}
	<div className="covidData__country__info">
		<p>Country Name : {country} </p>

		<p>Cases : {cases}</p>

		<p>Deaths : {deaths}</p>

		<p>Recovered : {recovered}</p>

		<p>Cases Today : {todayCases}</p>

		<p>Deaths Today : {deathCases}</p>

		<p>Recovered Today : {recoveredCases}</p>
	</div>
	</div>
    </center>
);
}

export default CovidData;
